import React from "react";

const Contact = () => {
  return (
    <div className="contact form-group">
      <button
        className="expand-button"
        type="button"
        data-target="#expand-contact"
      >
        Informacje kontaktowe
      </button>
      <div id="expand-contact" className="expand-section">
        <div className="company-name form-group">
          <label htmlFor="company-name">Nazwa firmy:</label>
          <input
            className="form-control"
            id="company-name"
            name="company-name"
            type="text"
          />
        </div>
        <div className="company-name form-group">
          <label htmlFor="company-name">NIP:</label>
          <input
            className="form-control"
            id="company-name"
            name="company-name"
            type="text"
          />
        </div>
        <div className="company-address form-group">
          <div>
            <input
              type="checkbox"
              name="diffrent-place-address"
              id="diffrent-place-address"
              className="mr-1"
            />
            <label htmlFor="diffrent-place-address">
              Adres lokalu jest inny niż adres siedziby?
            </label>
          </div>

          <label htmlFor="company-address">Adres siedziby:</label>
          <input
            className="form-control"
            id="company-address"
            name="company-address"
            type="text"
          />
        </div>
        <div className="place-address form-group collapse">
          <label htmlFor="place-address">Adres lokalu:</label>
          <input
            className="form-control"
            id="place-address"
            name="place-address"
            type="text"
          />
        </div>
        <div className="name">
          <label htmlFor="name">Imię i nazwisko:</label>
          <input className="form-control" id="name" name="name" type="text" />
        </div>
        <div className="email form-group">
          <label htmlFor="email">Email:</label>
          <input
            className="form-control"
            id="email"
            name="email"
            type="email"
          />
        </div>
        <div className="phone form-group">
          <label htmlFor="phone">Telefon:</label>
          <input className="form-control" id="phone" name="phone" type="text" />
        </div>
        <div className="message form-group">
          <label htmlFor="message">Wiadomość:</label>
          <textarea
            className="form-control"
            id="message"
            name="message"
          ></textarea>
        </div>
      </div>
    </div>
  );
};

export default Contact;
