import $ from "jquery";
import React, { useState } from "react";
import SoftwareQuestion from "../components/SoftwareQuestions";

const SoftwareImplementation = () => {
  const [modal, setModal] = useState(-1);
  const prefix = "software-implementation-";
  const offers = [
    "S4H POS",
    "S4H CHEF",
    "S4H KDS",
    "S4H SORTING",
    "S4H CATERING",
    "S4HONLINE Zamów",
    "S4H MOBILE POS",
    "S4H POS LITE",
    "S4H CRM",
    "S4H Hotel&Spa",
    "S4H ONLINE MiniBarek",
    "S4H BOOKING",
    "S4H ONLINE CleanRoom",
    "S4H HANDEL",
    "S4H ONLINE PanelManager",
    "S4H BI"
  ];
  const handleChange = index => {
    const input = $(`#${prefix}${index}`);
    if (input.prop("checked")) {
      setModal(index);
    } else {
      setModal(-1);
    }
  };

  const offerItems = offers.map((offer, index) => {
    return (
      <div key={`${prefix}${index}`} className="offer col-4 mb-1">
        {modal === index ? <SoftwareQuestion id={index} name={offer} /> : ""}
        {index === 0 ? (
          <input
            type="checkbox"
            name={`${prefix}${index}`}
            id={`${prefix}${index}`}
            className="mr-1"
            onChange={handleChange.bind(null, index)}
          />
        ) : (
          <input
            type="checkbox"
            name={`${prefix}${index}`}
            id={`${prefix}${index}`}
            className="mr-1"
          />
        )}
        <label htmlFor={`${prefix}${index}`}>{offer}</label>
      </div>
    );
  });

  return (
    <div className="software-implementation">
      <button
        className="expand-button"
        type="button"
        data-target="#expand-software-implementation"
      >
        Oprogramowanie - wdrożenie
      </button>
      <div id="expand-software-implementation" className="row">
        {offerItems}
      </div>
    </div>
  );
};

export default SoftwareImplementation;
