import $ from "jquery";
import React, { useEffect, useState } from "react";

const SoftwareQuestions = props => {
  const [seats, setSeats] = useState([]);
  const [seat, setSeat] = useState({ name: "", pos: false, newPos: false });
  const ShowModal = () => {
    $(`#question-${props.id}`).show("modal");
  };

  const hideElements = () => {
    document.querySelectorAll(".form .hide-me").forEach(element => {
      $(element).hide();
    });
  };

  const toggleHidden = targetName => {
    $(targetName)
      .parent()
      .toggle("slow");
  };

  const addEventListeners = () => {
    document.querySelectorAll(".form .show-me-more").forEach(element => {
      element.addEventListener(
        "click",
        toggleHidden.bind(null, element.getAttribute("data-target"))
      );
    });
  };

  const preventDefault = () => {
    document.querySelectorAll(".form button").forEach(button => {
      button.addEventListener("click", event => {
        event.preventDefault();
      });
    });
  };

  useEffect(() => {
    ShowModal();
    hideElements();
    addEventListeners();
    preventDefault();
  });

  const handleClick = () => {
    $(`#question-${props.id}`).hide("modal");
  };

  const handleAddSeats = event => {
    event.preventDefault();
    setSeats(prev => [...prev, seat]);
    setSeat({ name: "", pos: false, newPos: false });
  };

  const handleOnChange = (property, event) => {
    console.log(property);
    const newSeat = { name: seat.name, pos: seat.pos, newPos: seat.newPos };
    switch (property) {
      case "name":
        newSeat.name = event.target.value;
        break;
      case "pos":
        newSeat.pos = event.target.checked;
        break;
      case "newPos":
        newSeat.newPos = event.target.checked;
        break;
    }

    setSeat(newSeat);
  };

  const seatsItems = seats.map((seat, index) => {
    return (
      <div key={`seat-${index}`} className="btn btn-info">
        {seat.name}, pos: {seat.pos ? "true" : "false"}, newPos:{" "}
        {seat.newPos ? "true" : "false"}
      </div>
    );
  });

  return (
    <div
      className="modal"
      tabIndex="-1"
      role="dialog"
      id={`question-${props.id}`}
    >
      <div className="modal-dialog" role="document">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">{props.name}</h5>
            <button
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={handleClick}
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body">
            <div className="seats border p-2">
              <div className="form-group">
                <label htmlFor={`seat-name-${props.id}`}>
                  Nazwa stanowiska
                </label>
                <input
                  type="text"
                  id={`seat-name-${props.id}`}
                  className="form-control"
                  value={seat.name}
                  onChange={handleOnChange.bind(null, "name")}
                />
              </div>
              <div className="form-group">
                <input
                  type="checkbox"
                  id={`is-pos-termial${props.id}`}
                  className="mr-1"
                  checked={seat.pos}
                  onChange={handleOnChange.bind(null, "pos")}
                />
                <label htmlFor={`is-pos-termial${props.id}`}>
                  Czy stanowisko jest terminalem POS?
                </label>
              </div>
              <div className="form-group">
                <input
                  type="checkbox"
                  id={`new-pos${props.id}`}
                  className="mr-1"
                  checked={seat.newPos}
                  onChange={handleOnChange.bind(null, "newPos")}
                />
                <label htmlFor={`new-pos${props.id}`}>
                  Czy potrzebny nowy terminal?
                </label>
              </div>
              <button className="btn btn-info" onClick={handleAddSeats}>
                Dodaj stanowisko
              </button>
              <div className="d-flex flex-wrap">{seatsItems}</div>
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                id={`add-storages${props.id}`}
                className="mr-1 show-me-more"
                data-target={`#storages-count-${props.id}`}
              />
              <label htmlFor={`add-storages${props.id}`}>Dodać magazyny?</label>
            </div>
            <div className="form-group hide-me">
              <label htmlFor={`storages-count-${props.id}`}>
                Liczba magazynów
              </label>
              <input
                type="number"
                id={`storages-count-${props.id}`}
                className="form-control"
                min="0"
              />
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                id={`printed-in-kitchen${props.id}`}
                className="mr-1"
              />
              <label htmlFor={`printed-in-kitchen${props.id}`}>
                Czy zamówieniasą drukowane boniarkach w kuchni?
              </label>
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                id={`is-computer-network${props.id}`}
                className="mr-1"
              />
              <label htmlFor={`is-computer-network${props.id}`}>
                Czy w lokalu jest sieć komputerowa?
              </label>
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                id={`invoice-module${props.id}`}
                className="mr-1"
              />
              <label htmlFor={`invoice-module${props.id}`}>
                Chcesz dodać moduł fakturowania?
              </label>
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                id={`cash-bank-module${props.id}`}
                className="mr-1"
              />
              <label htmlFor={`cash-bank-module${props.id}`}>
                Chcesz dodać moduł Kasa/Bank?
              </label>
            </div>
            <div className="form-group">
              <label htmlFor={`cash-bank-module${props.id}`}>
                Czy obsługa przyjmuje zamówienia na tabletach? (podaj ilość)
              </label>
              <input
                type="number"
                id={`cash-bank-module${props.id}`}
                className="form-control"
                min="0"
              />
            </div>
            <div className="form-group">
              <input
                type="checkbox"
                id={`is-wifi${props.id}`}
                className="mr-1"
              />
              <label htmlFor={`is-wifi${props.id}`}>
                Czy w lokalu jest WiFi z bardzo dobrym zasięgiem, który obejmuje
                także sprzedaż poza lokalem np.. ogródek, taras?
              </label>
            </div>
            <div className="form-group">
              <button className="btn btn-info">Dodaj artykuły</button>
            </div>
            <div className="form-group">
              <button className="btn btn-info">Dodaj receptury</button>
            </div>
          </div>

          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              data-dismiss="modal"
              onClick={handleClick}
            >
              Zamknij
            </button>
            <button
              type="button"
              className="btn btn-primary"
              onClick={handleClick}
            >
              Zapisz zmiany
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SoftwareQuestions;
